import Vue from 'vue';
import Router from 'vue-router';
Vue.use(Router);

var Recommend = function Recommend() {
  return import('@/views/recommend/Recommend.vue');
};

var Hot = function Hot() {
  return import('@/views/hot/Hot.vue');
};

var Index = function Index() {
  return import('@/views/index.vue');
};

var Search = function Search() {
  return import('@/views/search/index.vue');
};

var SheetDetails = function SheetDetails() {
  return import('@/views/sheetdetails/SheetDetails');
};

var User = function User() {
  return import('@/views/user/User');
};

var Download = function Download() {
  return import('@/views/download/Download');
};

var PlaySong = function PlaySong() {
  return import('@/views/playsong/Playsong');
};

var Singer = function Singer() {
  return import('@/views/singer/index');
};

var Album = function Album() {
  return import('@/views/album/index');
};

var Mv = function Mv() {
  return import('@/views/MV/index');
};

export default new Router({
  // mode: 'history',   // 
  base: process.env.BASE_URL,
  routes: [{
    path: '/',
    component: Index,
    redirect: '/recommend',
    name: 'index',
    children: [{
      path: '/recommend',
      component: Recommend,
      name: 'recommend',
      meta: {
        title: ''
      }
    }]
  }, {
    path: '/hot',
    component: Index,
    children: [{
      path: '/hot',
      name: 'hot',
      component: Hot,
      meta: {
        title: ''
      }
    }]
  }, // 
  {
    path: '/search',
    component: Index,
    children: [{
      path: '/search',
      name: 'search',
      component: Search,
      meta: {
        title: ''
      }
    }]
  }, // 
  {
    path: '/playlist/detail',
    component: SheetDetails,
    name: 'sheetDetails'
  }, // 
  {
    path: '/user/playlist',
    component: User,
    name: 'user'
  }, // 
  {
    path: '/download',
    component: Download,
    name: 'download'
  }, // 
  {
    path: '/song',
    component: PlaySong,
    name: 'playsong'
  }, // 
  {
    path: '/singer',
    component: Singer,
    name: 'singer'
  }, // 
  {
    path: '/album',
    component: Album,
    name: 'album'
  }, // MV
  {
    path: '/mv',
    component: Mv,
    name: 'mv'
  }, // 
  {
    path: '/video',
    component: Mv,
    name: 'video'
  }]
});